/* NAV */

nav {
  width: 100%;
  background-color: #000;
  display: flex;
  align-items: center;
  height: 150px;
  color: #fff;
  justify-content: flex-end;
  padding: 15px 25px;
  box-sizing: border-box;
}

nav .nav-links-container a:not(:last-child) {
    margin-right: 20px;
    cursor: pointer;
}

nav img {
  max-width: 100px;
}

nav>a {
  margin-right: auto;
}

nav .nav-links-container a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  transition: opacity .2s linear;
}

nav .nav-links-container a:active {
  text-decoration: none;
}

nav .nav-link:hover {
  opacity: 0.8;
}



nav h3 {
  margin-right: auto;
}

.title-container {
  max-width: 1120px;
  width: 90%;
  margin: 0 auto 25px;
  font-size: 28px;
  text-align: center;
}

.movie-row {
  display: flex;
  padding: 25px;
  border-bottom: 1px solid gray;
  width: 90%;
  max-width: 1120px;
  margin: 0 auto;
}

.movie-row:last-child {
  border-bottom: none;
}

.movie-info-container, .movie-img-container {
  flex-grow: 0;
  flex-shrink: 0;
}

.movie-info-container {
  flex-basis: 80%;
  display: flex;
  flex-wrap: nowrap;
  align-content: flex-start;
  flex-direction: column;
}

.movie-info-container .movie-data-container {
  margin-top: auto;
  display: flex;
  align-content: flex-start;
  align-items: center;
}

.synopsis-overview {
  margin-bottom: 25px;
}
.movie-img-container {
  margin-right: 20px;
  flex-basis: 20%;
}

.view-btn.btn {
  padding: 10px 30px;
  border-radius: 5px;
  cursor: pointer;
  background-color: black;
  font-weight: bold;
  color: #fff;
  margin-left: auto;
  font-size: 14px;
  transition: all .25s linear;

}

.view-btn.btn:hover {
  background-color: #fff;
  color: #000;
  border: 1px solid #000;
}

.data {
  margin: 0 15px;
  font-weight: bold;
}

.movie-img-container img {
  width: 100%;
  max-width: 260px;
}

.pill {
  display: inline-block;
  padding: 7px 10px;
  border-radius: 5px;
  background-color: green;
  color: #fff;
  font-size: 14px;
}

.searchbox {
  display: block;
  width: 90%;
  max-width: 1120px;
  border: 1px solid #000;
  padding: 10px;
  font-size: 25px;
  margin: 25px auto;
}

@media (max-width: 767px) {

  nav {
    display: flex;
    flex-direction: column;
    height: 180px;
    padding: 25px;
  }

  nav a {
    display: block;
    margin-right: auto;
    margin-left: auto;
  }
  .movie-row {
    flex-direction: column;
  }

  .movie-info-container .movie-data-container {
    flex-direction: column;
    align-items: flex-start;
  }
  .nav-links-container {
    display: flex;
  }

  .view-btn.btn {
    margin-left: initial;
  }

  .movie-img-container img {
    margin: 0 auto;
    display: block;
  }
  .movie-info-container, .movie-img-container {
    flex-basis: 100%;
  }

  .movie-data-container span {
    margin-top: 15px;
    margin-bottom: 15px;
  }
}
